@media screen and(max-width: 1450px) {

}



.Links{
  height: 24px;
  left: 60.47%;
  right: 34.06%;
  top: calc(50% - 24px/2 - 19px);
  cursor: pointer;

  font-family: Fira Sans;
  font-style: normal;
  font-weight: bold;
  /*font-weight: 900;*/
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */
  text-align: center;
  text-decoration: none;
  padding-right: 17px;


  color: #007E62;
}

.langs{
  position: absolute;
  top: 20px;
  right: 20px;
  font-family: Fira Sans, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: -0.266667px;

  color: #734B3F;

  span{
    margin: 0 5px;
    cursor: pointer;
  }
}